import axios from 'axios'

const ERR_OK = 200
export function get (url) {
  return function (params) {
    return axios.get(url, {
      params
    }).then((res) => {
      // const { errno, data } = res.data
      const data = res.data.data
      const errno = res.data.code
      if (errno === ERR_OK) {
        return data
      } else {
        alert('没有数据，请稍后重试')
      }
    }).catch(() => {
    })
  }
}
